@import url('https://fonts.googleapis.com/css?family=Eagle+Lake');

* {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
}

.navbar {
	height: 70px;
	// width: 100%;
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	z-index: 999;
	padding: 15px;
	background: rgba(0, 0, 0, .2);
	border-bottom: 2px solid #fff;

	ul {
		display: flex;
		justify-content: center;
		list-style-type: none;

		li {
			margin-right: 30px;

			a {
			  display: block;
			  color: #fff;
			  font: 24px 'Eagle Lake', cursive;
			  text-decoration: none;
			  cursor: pointer;
			  transition: all 0.7s;
		    }

		    &:nth-child(1) {

		    	:hover {
		    		color: #ff9933;
		    	}
		    }

		    &:nth-child(2) {

		    	:hover {
		    		transform: scale(1.1);
		    	}
		    }

		    &:nth-child(3) {

		    	:hover {
		    		color: #128807;
		    	}
		    }

		    &:nth-child(4) {
		    	margin-left: auto;
		    	position: relative;

		    	&:after {
			         content: '';
			         position: absolute;
			         bottom: 0;
			         left: 0;
			         height: 3px;
			         width: 0;
			         transition: width .5s;
			         background: -webkit-gradient(linear, left top, right bottom, color-stop(0.5, #9fddf9), color-stop(0.8, #ffd700));
		             background: gradient(linear, left top, right bottom, color-stop(0.5, #9fddf9), color-stop(0.8, #ffd700));
		        }

		        &:hover {

			         &:after {
				          width: 100%;
				     }

			
		        }
		    }
		}
	
	}
}

.main-page {
	height: 100vh;
	background: url("../../img/IMG_6760.jpg") no-repeat;
	background-size: cover;
	background-position: 10% 20%;
	position: relative;

	&__filter {
		height: 100%;
		background: rgba(0, 0, 0, .4);
	}

	&__heading {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		font: 700 2rem 'Eagle Lake', cursive;
		color: #fff;
		text-align: center;

		img {
			height: 70px;
			width: 70px;
		}
	} 
	
}

