// 	width: 100%;
// }

.scroll-down {
	font: 300 1rem/1rem cursive;
	// text-transform: uppercase;
	letter-spacing: 2px;
	margin-top: 5rem;
}

.scroll-down__text {
	display: block;
}

.chevron-bottom {
	display: inline-block;
	width: 2rem;
	height: 2rem;
	margin-top: 2vh;
	background: url('../img/chevron.svg') center;
	background-size: cover;
	z-index: 10;
}

@-webkit-keyframes chevron{
	0% {top: 0;}
	50%{top: 10px;}
	100%{top: 0;}
}

@keyframes chevron{
	0% {top: 0;}
	50%{top: 10px;}
	100%{top: 0;}
}

.chevron-bottom:hover {
	position: relative;
	-webkit-animation-name: chevron;
	        animation-name: chevron;
	-webkit-animation-duration: 0.5s;
	        animation-duration: 0.5s;
}

.chevron-bottom--home {
	background: url('../img/chevron.svg') center;
	background-size: cover;
}

.normal-page {
	height: 100vh;
	padding: 90px 15px 15px 15px;


	h2 {
		text-align: center;
		margin-bottom: 20px;
		font: 2rem 'Eagle Lake', cursive;
		color: rgba(0, 0, 0, .9);
		text-shadow: 0 1px 0 #ff9933,
                     0 2px 0 #f98822,
                     0 3px 0 #eee,
                     0 4px 0 #e9e9e9,
                     0 5px 0 #ddd,
                     0 6px 1px rgba(0,0,0,.1),
                     0 0 5px rgba(0,0,0,.1),
                     0 1px 3px rgba(0,0,0,.3),
                     0 3px 5px rgba(0,0,0,.2),
                     0 5px 10px rgba(0,0,0,.25),
                     0 10px 10px rgba(0,0,0,.2),
                     0 20px 20px rgba(0,0,0,.15);
	}

	&--about {
		background: #ff9933;
		height: 130vh;


		.content {
			display: grid;
			grid-template-columns: repeat(2, 1fr);
			grid-gap: 10px;
			font: 1rem 'Eagle Lake', cursive;
			grid-auto-rows: 600px;

			h3 {
				margin-bottom: 10px;
				text-align: center;
			}

			.about-country {
				overflow: auto;
			}

			p {
				// overflow: auto;
				margin-bottom: 10px;
			}
		}
	}

	&--services {
		background: #fff;
		height: 162vh;
	}

	&--gallery {
		background: #128807;
		height: 100vh;
	}

	&--contacts {
		background: #fff;
		height: 90vh;
	}
}

.service-list {
	// border: 1px solid black;
	height: 85%;
	padding: 1em;
	overflow: auto;
	display: grid;
	grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
	grid-auto-rows: 360px;
	grid-gap: 15px;

	&__item {
		// border: 1px solid black;
		position: relative;
		overflow: auto;
		background: rgba(0, 0, 0, .2);

		

	}

	&__item-icon {
		// border-radius: 50%;
		// border: 1px solid black;
		// background: green;
		width: 100px;
		height: 100px;
		background: #fff;

		&--shuttle {
			background: url('../img/transfer.svg') top center no-repeat;
			background-size: 30%;
		}

		&--insurance {
			background: url('../img/medical-insurance.svg') top center no-repeat;
			background-size: 30%;
		}

		&--tourism {
			background: url('../img/tourism.svg') top center no-repeat;
			background-size: 30%;
		}

		&--visa {
			background: url('../img/visa.svg') top center no-repeat;
			background-size: 30%;
		}

		&--booking {
			background: url('../img/booking.svg') top center no-repeat;
			background-size: 30%;
		}

		&--legal {
			background: url('../img/legal.svg') top center no-repeat;
			background-size: 30%;
		}

		&--translate {
			background: url('../img/translate.svg') top center no-repeat;
			background-size: 30%;
		}

		&--conference {
			background: url('../img/conference.svg') top center no-repeat;
			background-size: 30%;
		}

	}

	&__item-content {
		text-align: center;
		height: 80%;
		width: 90%;
		// border: 1px solid black;
		position: absolute;
		top: 50px;
		left: 50%;
		transform: translateX(-50%);
		background-color: #fff; 
		font-family: cursive;
		padding: 5px;
		border-radius: 5px;
		color: #333;

		h3 {
			color: #000;
		}



	}

	&__item-icon, &__item-content {
		margin: 10px auto;
	}
}

// Contact form starts here

.contain {
  max-width: 1170px;
  margin-left: auto;
  margin-right: auto;
  padding: 1em;

}
@media (min-width: 600px) {
  .contain {
    padding: 0;
  }
}

h3,
ul {
  margin: 0;
}

h3 {
  margin-bottom: 1rem;
}

input:focus,
textarea:focus {
  outline: 2px solid #ff9933;
  box-shadow:4px 4px 20px rgba(18, 136, 7, .8);
}



.wrapper {
  box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.2);
  // padding-bottom: 5em;

  > * {
  // padding: 1em;
  }
}
 
@media (min-width: 700px) {
  .wrapper {
    display: grid;
    grid-template-columns: 1fr 2fr;
    grid-gap: 10px;


    // > * {
    // padding: 1.5em 2em;
    // }
  }
}

ul {
  list-style: none;
  padding: 0;
}

.contacts {
  background: rgba(0, 0, 0, .2);
  color: #fff;
  font: 1.5em 'Eagle Lake', cursive;
  padding: 0 1em;

  a {
  	color: #ff9933;
  	text-decoration: none;
  	font-variant: all-small-caps;
  	transition: all 0.3s;

  	&:hover, &:focus {
  		color: #fff;
  		text-shadow: 3px 0 1px rgba(18, 136, 7, .8);
  		outline: none;
  	}
  }
}

.form {
  background: #fff;
  font-family: cursive;
}

form {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 10px;

  label {
  display: block;
  }

  p {
  margin: 0;
  }

  input,
  textarea,
  button {
      width: 100%;
      padding: 1em;
  }

  input,
  textarea {
  	border: 1px solid #000;
  }

  textarea {
  	resize: none;
  }
}
 
.full-width {
  grid-column: 1 / 3;
}

button {
  background: lightgrey;
  width: 100%;
  font: 900 1.2em/0.5em 'Eagle Lake', cursive;
  font-variant: small-caps;
  transition: background 0.7s, color 0.1s;


  &:hover, &:focus {
  background: rgb(18, 136, 7);
  outline: 0;
  cursor: pointer;
  color: #fff;
  box-shadow:4px 4px 20px rgba(18, 136, 7, .8);
  border: none;
  }
}

// Slider starts here

.swiper-container {
	width: 550px;
    height: 436px;

}

.photo-gallery {
	display: flex;
	padding: 0 5%;
	flex-wrap: wrap;
	justify-content: center;
	align-items: center;
}

.image {
	width: 16%;
	margin: 0 2% 2% 0;
}

.image img {
	width: 100%;
	height: auto;
	// cursor: pointer;
	transition: 0.5s;
}

.image img:hover {
	transform: scale(1.7, 1.7);
}

// button back-to-top starts here

#button {
  display: inline-block;
  background-color: rgba(255, 153, 51, .5);
  width: 50px;
  height: 50px;
  text-align: center;
  border-radius: 4px;
  position: fixed;
  bottom: 30px;
  right: 30px;
  transition: background-color .3s, 
    opacity .5s, visibility .5s;
  opacity: 0;
  visibility: hidden;
  z-index: 1000;
}
#button::after {
  content: "\f077";
  font-family: FontAwesome;
  font-weight: normal;
  font-style: normal;
  font-size: 2em;
  line-height: 50px;
  color: #fff;
}
#button:hover {
  cursor: pointer;
  background-color: #333;
}
#button:active {
  background-color: #555;
}
#button.show {
  opacity: 1;
  visibility: visible;
}

