  vertical-align: middle;
  width: 36px;
  height: 36px;
  margin-right: 16px;
  border-radius: 50%;
  text-align: center;
  font-size: 14px;
  line-height: 36px;
  color: #FFF;
  text-decoration: none;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

.icon:last-child {
  margin-right: 0;
}

.icon--facebook {
  background: #3162a2 url('../img/facebook-icon.svg') no-repeat;
  background-size: cover;
}

.icon--facebook:hover {
  background-color: #295185;
}

.icon--twitter {
  background: #2eb1eb url('../img/twitter-icon.svg') no-repeat;
  background-size: cover;;
}

.icon--twitter:hover {
  background-color: #1D86B4;
}

.icon--linkedin {
  background: #0e76A8 url('../img/linkedin-icon.svg') no-repeat;
  background-size: cover;
}

.icon--linkedin:hover {
  background-color: #BE428E;
}

.icon--google-plus {
  background: #f25c58 url('../img/google-plus-icon.svg') no-repeat;
  background-size: cover;
}

.icon--google-plus:hover {
  background-color: #C3504D;
}

@media (max-width: 650px) {
  .footer__nav-link:not(:first-child) {
